import { createSelector } from "reselect";
var getUsersSelector = function (state) {
    return state.usersPage.users;
};
export var getUsers = createSelector(getUsersSelector, function (users) {
    return users.filter(function (u) { return true; });
});
export var getPageSize = function (state) {
    return state.usersPage.pageSize;
};
export var getTotalUsersCount = function (state) {
    return state.usersPage.totalUsersCount;
};
export var getCurrentPage = function (state) {
    return state.usersPage.currentPage;
};
export var getIsFetching = function (state) {
    return state.usersPage.isFetching;
};
export var getFollowingInProgress = function (state) {
    return state.usersPage.followingProgress;
};
